import { useState }  from 'react';
import './App.css';
import 'fontsource-roboto';
//Material-UI
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

const App = () => {
  const [name, setName] = useState('');
  const [gender, setGender] = useState('');

  //ref inputs
  const [behaviorScore, setBehaviorScore] = useState(null);
  const [speakingScore, setSpeakingScore] = useState(null);
  const [readingScore, setReadingScore] = useState(null);
  const [writingScore, setWritingScore] = useState(null);

  const [comment, setComment] = useState('');

  const refData = {
    behavior: {
      good: [
        `${name} always works very diligently when ${gender === 'male' ? 'he' : 'she'} is focused.`,
        `It's nice to see ${gender === 'male' ? 'him' : 'her'} well-behaved in the classroom.`,
        `It's a pleasure watching ${gender === 'male' ? 'him' : 'her'} focus on the classwork.`
      ],
      average: [
        `${gender === 'male' ? 'He' : 'She'} is average.`,
        `${gender === 'male' ? 'He' : 'She'} is okay.`,
        `${gender === 'male' ? 'He' : 'She'} is so-so.`
      ],
      bad: [
        `${gender === 'male' ? 'He' : 'She'} is bad.`,
        `${gender === 'male' ? 'He' : 'She'} is not good.`,
        `${gender === 'male' ? 'He' : 'She'} is inadequate.`
      ]
    },
    reading: {
      good: [
        `${gender === 'male' ? 'He' : 'She'} is good.`,
        `${gender === 'male' ? 'He' : 'She'} is excellent.`,
        `${gender === 'male' ? 'He' : 'She'} is wonderful.`
      ],
      average: [
        `${gender === 'male' ? 'He' : 'She'} is average.`,
        `${gender === 'male' ? 'He' : 'She'} is okay.`,
        `${gender === 'male' ? 'He' : 'She'} is so-so.`
      ],
      bad: [
        `${gender === 'male' ? 'He' : 'She'} is bad.`,
        `${gender === 'male' ? 'He' : 'She'} is not good.`,
        `${gender === 'male' ? 'He' : 'She'} is inadequate.`
      ]
    },
    speaking: {
      good: [
        `${gender === 'male' ? 'He' : 'She'} is good.`,
        `${gender === 'male' ? 'He' : 'She'} is excellent.`,
        `${gender === 'male' ? 'He' : 'She'} is wonderful.`
      ],
      average: [
        `${gender === 'male' ? 'He' : 'She'} is average.`,
        `${gender === 'male' ? 'He' : 'She'} is okay.`,
        `${gender === 'male' ? 'He' : 'She'} is so-so.`
      ],
      bad: [
        `${gender === 'male' ? 'He' : 'She'} is okay.`,
        `${gender === 'male' ? 'He' : 'She'} is bad.`,
        `${gender === 'male' ? 'He' : 'She'} is inadequate.`
      ]
    },
    writing: {
      good: [
        `${gender === 'male' ? 'He' : 'She'} is good.`,
        `${gender === 'male' ? 'He' : 'She'} is excellent.`,
        `${gender === 'male' ? 'He' : 'She'} is wonderful.`
      ],
      average: [
        `${gender === 'male' ? 'He' : 'She'} is average.`,
        `${gender === 'male' ? 'He' : 'She'} is okay.`,
        `${gender === 'male' ? 'He' : 'She'} is so-so.`
      ],
      bad: [
        `${gender === 'male' ? 'He' : 'She'} is okay.`,
        `${gender === 'male' ? 'He' : 'She'} is bad.`,
        `${gender === 'male' ? 'He' : 'She'} is inadequate.`
      ]
    }
  }



  //FUNCTIONS
  //construct comment
  function constructComment(e) {
  e.preventDefault();
  
  const numGen = () => Math.floor(Math.random() * 3);

  let behaviorComment = refData.behavior[behaviorScore][numGen()];
  let readingComment = refData.reading[readingScore][numGen()];
  let speakingComment = refData.speaking[speakingScore][numGen()];
  let writingComment = refData.writing[writingScore][numGen()];

  setComment(`${behaviorComment} ${speakingComment} ${readingComment} ${writingComment}`)

  }

  return (
    <div className="App">
      
      <Container maxWidth="md">
        <Grid container  justify="center" spacing={3}>
          <Grid item xs={12}>
          <h1>Comment generator</h1>
          <TextField label="Name" value={name} onChange={(e) => setName(e.target.value)} />

          </Grid>
          {/* Gender */}
          <Grid item xs={12}>
            <FormControl component="fieldset">
              <FormLabel component="legend">Gender</FormLabel>
              <RadioGroup row aria-label="gender" name="gender" value={gender} onChange={(e) => setGender(e.target.value)}>
                <FormControlLabel value="male" control={<Radio />} label="Male" />
                <FormControlLabel value="female" control={<Radio />} label="Female" />
              </RadioGroup>
            </FormControl>
          </Grid>
          {/* Behavior */}
          <Grid item xs={12}>
            <FormControl component="fieldset">
              <FormLabel component="legend">Behavior</FormLabel>
              <RadioGroup row  aria-label="behavior" name="behavior" value={behaviorScore} onChange={(e) => setBehaviorScore(e.target.value)}>
                <FormControlLabel value="good" control={<Radio />} label="Good" labelPlacement="bottom" />
                <FormControlLabel value="average" control={<Radio />} label="Average" labelPlacement="bottom" />
                <FormControlLabel value="bad" control={<Radio />} label="Bad" labelPlacement="bottom" />
              </RadioGroup>
            </FormControl>
          </Grid>
          {/* Speaking */}
          <Grid item xs={12} md={4}>
            <FormControl component="fieldset">
              <FormLabel component="legend">Speaking</FormLabel>
              <RadioGroup row  aria-label="speaking" name="speaking" value={speakingScore} onChange={(e) => setSpeakingScore(e.target.value)}>
                <FormControlLabel value="good" control={<Radio />} label="Good" labelPlacement="bottom" />
                <FormControlLabel value="average" control={<Radio />} label="Average" labelPlacement="bottom" />
                <FormControlLabel value="bad" control={<Radio />} label="Bad" labelPlacement="bottom" />
              </RadioGroup>
            </FormControl>
          </Grid>
          {/* Reading */}
          <Grid item xs={12} md={4}>
            <FormControl component="fieldset">
              <FormLabel component="legend">Reading</FormLabel>
              <RadioGroup row  aria-label="reading" name="reading" value={readingScore} onChange={(e) => setReadingScore(e.target.value)}>
                <FormControlLabel value="good" control={<Radio />} label="Good" labelPlacement="bottom" />
                <FormControlLabel value="average" control={<Radio />} label="Average" labelPlacement="bottom" />
                <FormControlLabel value="bad" control={<Radio />} label="Bad" labelPlacement="bottom" />
              </RadioGroup>
            </FormControl>
          </Grid>
          {/* Writing */}
          <Grid item xs={12} md={4}>
            <FormControl component="fieldset">
              <FormLabel component="legend">Writing</FormLabel>
              <RadioGroup row  aria-label="writing" name="writing" value={writingScore} onChange={(e) => setWritingScore(e.target.value)}>
                <FormControlLabel value="good" control={<Radio />} label="Good" labelPlacement="bottom" />
                <FormControlLabel value="average" control={<Radio />} label="Average" labelPlacement="bottom" />
                <FormControlLabel value="bad" control={<Radio />} label="Bad" labelPlacement="bottom" />
              </RadioGroup>
            </FormControl>
          </Grid>
          <Button variant="contained" color="primary" onClick={(e) => constructComment(e, name)}>
              Generate comment
          </Button>
        </Grid>
        
      </Container>

      <div>
        <h3>Comment</h3>
        <p>{comment}</p>
      </div>

    </div>
  );
}

export default App;
